.contact-list {
	background-color: rgba(17, 27, 33, 0.9);

	header {
		display: flex;
		height: 100px;
		align-items: center;
		padding-left: 20px;
		border-bottom: 1px solid rgba(204, 204, 204, 0.5);
		gap: 20px;
		background: #202c33;
		color: #aebac1;
		min-width: 400px;
		justify-content: space-between;

		.user-div {
			flex: 1;
			display: flex;
			align-items: center;
			gap: 20px;
		}

		.profile {
			width: 50px;
			height: 50px;
			border-radius: 50%;
			background-color: #fff;
			cursor: pointer;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
		}

		h3 {
			cursor: default;
		}

		.icon {
			width: 50px;
			height: 50px;
			border-radius: 50%;
			cursor: pointer;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			text-align: center;
			line-height: 50px;
			font-size: 36px;
			font-size: 2rem;
		}
	}

	.search-bar-container {
		width: 100%;
		height: 65px;
		display: flex;
		justify-content: center;
		display: flex;
		justify-content: center;
		align-items: center;
		border-bottom: 1px solid rgba(204, 204, 204, 0.5);

		.search-input {
			width: 90%;
			height: 40px;
			border: none;
			outline: none;
			font-size: 20px;
			padding-left: 20px;
			border-radius: 10px;
		}
	}

	main {
		.contact-list__title {
			font-size: 18px;
			margin-left: 20px;
			text-decoration: underline;
			cursor: default;
			margin-bottom: 10px;
			font-weight: 500;
			color: #aebac1;
		}
	}
}
