.application {
  display: flex;
  height: 100vh;
  width: 100%;
}
.application .profile__section {
  display: none;
}
.application .profile.profile--open + .profile__section {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  width: 650px;
  height: 50%;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}
.application .profile.profile--open + .profile__section h1 {
  text-align: center;
}
.application .profile.profile--open + .profile__section .profile__form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  justify-content: center;
  height: 85%;
}
.application .profile.profile--open + .profile__section .profile__form input {
  width: 50%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  font-size: 18px;
}
.application .profile.profile--open + .profile__section .profile__form .btn-container {
  width: 60%;
  display: flex;
  justify-content: space-around;
}
.application .profile.profile--open + .profile__section .profile__form .btn-container button {
  width: 150px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  font-size: 18px;
  text-align: center;
  cursor: pointer;
}
